.champion-div {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	grid-auto-rows: 1fr;
	grid-gap: 10px;
	padding: 10px;
	text-align: center;
	justify-items: center;
}

.champion-list {
	width: 50%;
	object-fit: contain;
	display: flex;
	justify-self: center;
	align-self: center;
	flex-direction: column;
	cursor: pointer;
	/* border: 3px solid brown; */
}

.champion-list span {
	margin: 0;
}
