.main-banner {
  position: relative;
}

.main-banner img {
  width: 100%;
}

.links-holder {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 10px;
  background: transparent;
  background-color: rgba(255, 255, 255, 0.3);
}

.links-holder div {
  color: white
}

#primary-links {
  display: flex;
  padding-right: 10px;
}

#primary-links a {
  color: white;
  font-size: 12px;
  font-weight: bold;
  text-decoration: none;
}

#primary-links a img {
  height: 16px;
  width: 16px;
  margin-right: 5px;
}

#secondary-links a {
  align-items: center;
}

#secondary-links a[title="페이스북"] {
  padding-right: 10px;
}

#secondary-links a img {
  height: 16px;
  width: 16px;
}

.primary-flex {
  display: inline;
}
